import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as Utils from '../../../tools/closure'
import { browserHistory } from 'react-router'

import '../styles.scss'
import BraftEditor from 'braft-editor'
import 'braft-editor/dist/braft.css'

import message from 'antd/lib/message'
import 'antd/lib/message/style/index.css'

import LabelSelect from './LabelSelect'

export default class WriteView extends Component {
  static propTypes = {
    uid: PropTypes.string.isRequired,
    currentState: PropTypes.string.isRequired,
    currentPostID: PropTypes.string.isRequired,
    saved: PropTypes.func.isRequired,
    saving: PropTypes.func.isRequired,
    saveFailed: PropTypes.func.isRequired,
  }

  lables = []
  lastTime = 0
  getLabels = (labels) => {
    this.labels = labels
  }

  publish = () => {
    const { saved, saving, saveFailed, currentPostID } = this.props
    debugger
    saving()
    fetch('/back/index.php?s=/index/note/publish', {
      method: 'POST',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ nid: currentPostID })
    }).then(res => {
      if (res.status >= 200 && res.status < 300) {
        return res.json()
      }
    }).then((data) => {
      debugger
      if (data.code === -1) {
        message.error(`, ${data.msg}`)
        saveFailed(data.msg)
      } else if (data.code === 0) {
        saved(currentPostID)
        message.success('!')
      }
    }).catch(error => {
      console.log(error)
      saveFailed(error)
      message.error('!')
    })
  }
  save = (content) => {
    const { saved, saving, saveFailed, currentPostID } = this.props
    debugger
    saving()
    message.info('..')
    fetch('/back/index.php?s=/index/note/refresh', {
      method: 'POST',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        nid: currentPostID,
        title: this.postTitle.value,
        content,
        labels: JSON.stringify(this.labels || [])
      })
    }).then(res => {
      if (res.status >= 200 && res.status < 300) {
        return res.json()
      }
    }).then((data) => {
      debugger
      if (data.code === -1) {
        message.error(`, ${data.msg}`)
        saveFailed(data.msg)
      } else if (data.code === 0) {
        saved(currentPostID)
        message.success('')
      }
    }).catch(error => {
      console.log(error)
      saveFailed(error)
      message.error('!')
    })
  }

  handleHTMLChange = (htmlContent) => {
    debugger
    
    if (htmlContent !== '<p></p>') {
      if (!this.lastTime) {
        this.lastTime = Date.now()
      } else {
        if (Date.now() - this.lastTime > 5000) {
          this.save(htmlContent)
          this.lastTime = Date.now()
        }
      }
    }
  }

  componentDidMount () {
    const { saved, saveFailed } = this.props
    fetch('/back/index.php?s=/index/note/add', {
      credentials: 'include',
      headers: {
        withCredentials: true
      }
    })
    .then(res => {
      if (res.status >= 200 && res.status < 300) {
        return res.json()
      }
    }).then((data) => {
      debugger
      if (data.code === -1) {
        message.error(`, ${data.msg}`)
        saveFailed(data.msg)
        browserHistory.goBack()
      } else if (data.code === 0) {
        saved(data.msg.nid)
      }
    }).catch(error => {
      console.log(error)
      saveFailed(error)
      message.error('!')
      browserHistory.goBack()
    })
  }

  render () {
    const { uid } = this.props

    const editorProps = {
      className: 'editor',
      ref: instance => { this.editorInstance = instance },
      placeholder: '',
      contentFormat: 'html',
      onChange: this.handleChange,
      onRawChange: this.handleRawChange,
      onHTMLChange: this.handleHTMLChange
    }

    return (
      <div className='write'>
        <div className='container'>
          <div className='row'>
            <div className='col-sm-12 col-md-9 col-md-offset-2'>
              <div className='title-input-container'>
                <textarea className='title'
                  id='richtext-title'
                  name='title'
                  placeholder=''
                  ref={instance => { this.postTitle = instance }}
                  />
              </div>
              <BraftEditor {...editorProps} />
            </div>
          </div>
          <div className='row'>
            <div className='col-sm-12 col-md-9 col-md-offset-2'>
              <LabelSelect letFatherKnowLabels={this.getLabels} />
            </div>
          </div>
          <div className='row'>
            <div className='col-sm-12 col-md-3 col-md-offset-8'>
              <div className='publish' onClick={this.publish}></div>
            </div>
          </div>
        </div>

      </div>
    )
  }
}



